import '../ControlPanelStyle.css';
import firebase from 'firebase';
import Comps from '../companies.json';

var ComDep;
var Data;
var Data1;
var Data2;
var Data3;
var GetUser;
var boss;

function GetComp(cd) {
  Comps.map(function (company) {
    if(company.company === cd){
      ComDep = (company.companyname + " " + company.department);
    }
    return ComDep;
  });
}

const Dashboard = (props) => {
  GetUser = firebase.database().ref("User").orderByKey().equalTo(props.id);
  GetUser.once('value', (snapshot) => {
    snapshot.forEach((childSnapshot) => {
      Data = childSnapshot.val();
    })
    if(Data !== undefined){
      GetComp(Data.company);
      GetUser = firebase.database().ref("Staff");
      GetUser.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Data1 = childSnapshot.val();
          for(var i = 0; i <= Data1.company.length; i = i + 6){
            if((Data1.company.slice(i, i + 4) === Data.company)&&((Data1.role === "Skyriaus vadovas")||(Data1.role === "Skyriaus vadovė"))){
              boss = Data1.name + " " + Data1.surname;
            }
          }
          document.getElementById("worker").innerHTML = "<b>Darbuotojas:</b> " + Data.name + " " + Data.surname;
          document.getElementById("worker-role").innerHTML = "<b>Pareigos:</b> " + Data.role;
          document.getElementById("worker-company").innerHTML = "<b>Įmonė:</b> " + ComDep;
          document.getElementById("worker-boss").innerHTML = "<b>Vadovas:</b> " + boss;
          document.getElementById("worker-days").innerHTML = "<b>Atostoginės dienos:</b> " + document.getElementById("user-days").innerHTML.slice(16);
        })
      })
    }
    else{
      GetUser = firebase.database().ref("Staff").orderByKey().equalTo(props.id);
      GetUser.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Data2 = childSnapshot.val();
        })
        if(Data2 !== undefined){
          GetComp(Data2.company);
          GetUser = firebase.database().ref("Staff");
          GetUser.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
              Data3 = childSnapshot.val();
              for(var i = 0; i <= Data3.company.length; i = i + 6){
                if((Data3.workCompany.slice(i, i + 4) === Data2.company)&&((Data2.role === "Buhalteris")||(Data2.role === "Buhalterė"))&&((Data3.role === "Skyriaus vadovas")||(Data3.role === "Skyriaus vadovė"))){
                  boss = Data3.name + " " + Data3.surname;
                }
                if(((Data3.workCompany.slice(i, i + 2) === Data2.company.slice(0, 2)&&(Data3.workCompany.slice(i + 2, i + 4) === "00"))||(Data3.workCompany.slice(i, i + 4) === Data2.company))&&((Data2.role === "Skyriaus vadovas")||(Data2.role === "Skyriaus vadovė"))&&((Data3.role === "Padalinio vadovas")||(Data3.role === "Padalinio vadovė"))){
                  boss = Data3.name + " " + Data3.surname;
                }
                if(((Data3.workCompany.slice(i, i + 2) === Data2.company.slice(0, 2)&&(Data3.workCompany.slice(i + 2, i + 4) === "00"))||(Data3.workCompany.slice(i, i + 4) === Data2.company))&&((Data2.role === "Padalinio vadovas")||(Data2.role === "Padalinio vadovė"))&&((Data3.role === "Generalinis direktorius")||(Data3.role === "Generalinė direktorė"))){
                  boss = Data3.name + " " + Data3.surname;
                }
                if((Data2.role === "Generalinis direktorius")||(Data2.role === "Generalinė direktorė")){
                  boss = Data2.name + " " + Data2.surname;
                }
              }
              document.getElementById("worker").innerHTML = "<b>Darbuotojas:</b> " + Data2.name + " " + Data2.surname;
              document.getElementById("worker-role").innerHTML = "<b>Pareigos:</b> " + Data2.role;
              document.getElementById("worker-company").innerHTML = "<b>Įmonė:</b> " + ComDep;
              document.getElementById("worker-boss").innerHTML = "<b>Vadovas:</b> " + boss;
              document.getElementById("worker-days").innerHTML = "<b>Atostoginės dienos:</b> " + document.getElementById("user-days").innerHTML.slice(16);
            })
          })
        }
        if(document.getElementById("user-role").innerHTML === "Administratorius"){
          //document.getElementById("worker").innerHTML = "Administratoriaus paskyra norint pasiekti savo duomenis prisijunkite prie vartotojo paskyros";
        }
      })
    }
  })
  return (
    <div className="dashboard-page">
      <h2 className="dash-info-top">Pradinis</h2>
      <div id="dash-info-wrap" className="dash-info-wrap">
        <div className="dash-info" id="worker"></div>
        <div className="dash-info" id="worker-role"></div>
        <div className="dash-info" id="worker-company"></div>
        <div className="dash-info" id="worker-boss"></div>
        <div className="dash-info" id="worker-days"></div>
      </div>
    </div>
  );
};

export default Dashboard;