import './controlpanel/ControlPanelStyle.css';
import ReactDOM from 'react-dom';
import Login from './controlpanel/Login';
import Register from './controlpanel/Register';
import LoginAdmin from './controlpanel/LoginAdmin';
import LoginStaff from './controlpanel/LoginStaff';
import logo from './logo.png';
import './Firebase';

function Start() {
  return (
    <div className="container page-start">
      <img src={logo} className="logo" alt="Hegelmann logo"/>
      <div className="small-container">
        <div id="user" className="content">
          <div className="cred-row">
            <h2>Sveiki, atvykę!</h2>
          </div>
          <div className="buttons">
            <button className="menubutton" onClick={() => ReactDOM.render(<Login />, document.getElementById('root'))}>Prisijungimas</button>
            <button className="menubutton" onClick={() => ReactDOM.render(<Register />, document.getElementById('root'))}>Registracija</button>
          </div>
          <div class="other-logins">
            <h6 onClick={() => ReactDOM.render(<LoginAdmin />, document.getElementById('root'))}>administorius</h6>
            <h6 style={{color: "#686a6c", paddingInline: "5px"}}>|</h6>
            <h6 onClick={() => ReactDOM.render(<LoginStaff />, document.getElementById('root'))}>moderatorius</h6>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Start;