import './ControlPanelStyle.css';
import NavBar from './NavBar';
import SideBar from './SideBar';
import { BiLogOut } from "react-icons/bi";
import MenuIcon from '@material-ui/icons/Menu';

const ControlPanel = (props) => {
  return (
    <div className="panel-page">
      <div className="nav-misc">
        <div>
          <MenuIcon id="hamburger" onClick={() => Hamburger()} className="hamburger" />
        </div>
        <div>
          <BiLogOut id="logout" onClick={() => window.location.reload(false)} className="logout-logo" />
        </div>
      </div>
      <div id="navbar" className="navbar">        
        <NavBar id={props.id}/>
      </div>
      <div id="sidebar" className="sidebar">
        <SideBar id={props.id}/>
      </div>
    </div>
  );
};

function Hamburger() {
  if(document.getElementById("navbar").style.display === "none"){
    Expand();
  }
  else{
    Collapse();
  }
};

function Collapse() {
  return (
    document.getElementById("navbar").style.display = "none",
    document.getElementById("sidebar").style.width = "100%",
    document.getElementById("hamburger").style.border = "none"
  );
};

function Expand() {
  return (
    document.getElementById("navbar").style.display = "block",
    document.getElementById("sidebar").style.width = "calc(100% - 300px)",
    document.getElementById("hamburger").style.border = "none"
  );
};

export default ControlPanel;