import * as React from 'react';
import firebase from 'firebase';
import ReactDOM from 'react-dom';
import '../ControlPanelStyle.css';
import MaterialTable from 'material-table';
import { forwardRef } from 'react';
import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Comps from '../companies.json'
import maildata from '../maildata.json';

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
};

var row;
var GetTableData;
var GetCompany;
var Data;
var Data1;
var array = [];
var rows = [];
var comp;
var ComDep;
var act;

function GetComp(cd) {
  Comps.map(function (company) {
    if(company.company === cd){
      ComDep = (company.companyname + " " + company.department);
    }
    return ComDep;
  });
}
export default function DataList(props) {
  const { useState } = React;
  const [selectedRow, setSelectedRow] = useState(null);

  return (
    array.length = 0,
    GetCompany = firebase.database().ref("Staff").orderByKey().equalTo(props.id),
    GetCompany.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data1 = childSnapshot.val();
          comp = Data1.workCompany;
          if((Data1.role === "Buhalteris")||(Data1.role === "Buhalterė")){
            act = {
              onRowAdd: newData =>
              new Promise((resolve, reject) => {
                setTimeout(() => {
                  TableActionAdd(newData.name, newData.surname, newData.email, newData.days, newData.code, newData.company, newData.role);
                  ReactDOM.render(<DataList id={props.id}/>, document.getElementById('panelroot'));
                  resolve();
                }, 1000)
              })
            };
          }
          else act = false;
      });
      GetTableData = firebase.database().ref("User");
      GetTableData.on('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
          var tempdate = new Date();
          var TD = (tempdate.getMonth() + 1) + " " + tempdate.getDate() + " " + tempdate.getFullYear();
          var date1 = new Date(Data.regDate);  
          var date2 = new Date(TD); 
          var time_diff = date2.getTime() - date1.getTime();
          var DD = time_diff / (1000 * 60 * 60 * 24);
          DD = Math.round(DD);
          var date = date1;
          var WE;
          var days = 0;
          for(var i = 0; i < DD; i++){
            WE = date.getDay();
            days = days + 0.079365;
            if(WE % 6 === 0){days = days - 0.079365;}
            if((date.getMonth() === 1)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 2)&&(date.getDate() === 16)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 3)&&(date.getDate() === 11)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 4)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 5)&&(date.getDate() === 1)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 6)&&(date.getDate() === 24)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 7)&&(date.getDate() === 6)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 8)&&(date.getDate() === 15)&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 11)&&(date.getDate() === 1 )&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 11)&&(date.getDate() === 2 )&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 12)&&(date.getDate() === 24 )&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 12)&&(date.getDate() === 25 )&&(WE % 6 !== 0)){days = days - 0.079365;}
            if((date.getMonth() === 12)&&(date.getDate() === 26 )&&(WE % 6 !== 0)){days = days - 0.079365;}
            date.setDate(date.getDate() + 1);
          }
          for(var j = 0; j <= comp.length; j = j + 6){
            if(((comp.slice(j, j + 2) === Data.company.slice(0, 2))&&(comp.slice(j + 2, j + 4) === "00"))||((comp.slice(j, j + 4) === Data.company))){
              GetComp(Data.company);
              row ={
                id: Data.id,
                code: Data.code,
                name: Data.name,
                surname: Data.surname,
                email: Data.email,
                days: (parseInt(Data.days) + days).toFixed(3),
                company: ComDep,
                role: Data.role
              };
                array.push(row);
            }
          }
        });
        rows = array.map((arr) => arr);
      });
    }),
    <div>
      <div style={{ maxWidth: '100%' }}>
          <MaterialTable
            icons={tableIcons}
            localization={{
              pagination: {
                labelRowsSelect: 'eilutės',
                firstTooltip: 'Pirmas puslapis',
                nextTooltip: 'Sekantis puslapis',
                previousTooltip: 'Ankstesnis puslapis',
                lastTooltip: 'Paskutinis puslapis'
              },
              toolbar: {
                searchPlaceholder: 'Ieškoti',
                searchTooltip: 'Ieškoti'
              },
              header: {
                actions: 'VEIKSMAI'
              },
              body: {
                emptyDataSourceMessage: 'Nėra įkeltų duomenų',
                filterRow: {
                  filterTooltip: 'Filtruoti'
                },
                editTooltip: 'Redaguoti',
                deleteTooltip: 'Ištrinti',
                addTooltip: 'Pridėti naują',
                editRow: {
                  deleteText: 'Ar tikrai norite ištrinti vartotoją?',
                  cancelTooltip: 'Atšaukti',
                  saveTooltip: 'Patvirtinti'
                }
              }
            }}
            columns={[
              { title: 'ID', field: 'id', type: 'numeric', editable: 'never', align: 'left' },
              { title: 'ASMENS KODAS', field: 'code', editable: 'onAdd', align: 'left' },
              { title: 'VARDAS', field: 'name', editable: 'onAdd', align: 'left' },
              { title: 'PAVARDĖ', field: 'surname', editable: 'onAdd', align: 'left' },
              { title: 'PAREIGOS', field: 'role', editable: 'onAdd', align: 'left' },
              { title: 'EL.PAŠTAS', field: 'email', editable: 'onAdd', align: 'left'},
              { title: 'DIENOS', field: 'days', type: 'numeric', editable: 'onAdd', align: 'left' },
              { title: 'ĮMONĖ', field: 'company', editable: 'onAdd', align: 'left' }
            ]}
            data={rows}
            title="Darbuotojų sąrašas"
            onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
            options={{
              actionsColumnIndex: -1,
              pageSizeOptions: [5, 10],
              pageSize: 10,
              addRowPosition: 'first',
              filtering: true,
              headerStyle: {
                fontWeight: 'bold',
                fontSize: '16px'
              },
              titleStyle:{
                fontWeight: 'bold'
              },
              rowStyle: rowData => ({
                backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
              })
            }}

            editable={act}
          />
        </div>
        <button className="page-buttons" onClick={() => ReactDOM.render(<DataList id={props.id}/>, document.getElementById('panelroot'))}>Įkelti / Atnaujinti duomenis</button>
      </div>
  );
}

var check;
var AddData;
var Auid;
var AddTableData;

export function TableActionAdd(name, surname, email, days, code, company, role) {
  check = 0;
  var date = new Date();
  var reg_date = (date.getMonth() + 1) + " " + date.getDate() + " " + date.getFullYear();
  let userid = () => {
    return Math.floor((1 + Math.random()) * 0x100000).toString(16);
  }
  var username = userid();
  if((name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)&&(days !== undefined)&&(code !== undefined)){
    return (
      AddTableData = firebase.database().ref("User"),
      AddTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          AddData = childSnapshot.val();
          Auid = AddData.id;
          if(AddData.code === code){
            check = 1;
          }
        });
        if(check === 0){
          GetComp(company);
          var addUser = firebase.database().ref("User");
          var addNewUser = addUser.push();
          addNewUser.set({
            id: (Auid + 1),
            name: name,
            surname: surname,
            email: email,
            days: days,
            code: code,
            password: "",
            company: company,
            regDate: reg_date,
            userid: username,
            role: role
          })
          window.Email.send({
            Host: "smtp.gmail.com",
            Username: maildata.emailname,
            Password: maildata.emailpassword,
            To: email,
            From: maildata.emailname,
            FromName: ComDep,
            Subject: "Registracija",
            Body: "Sveiki <b>" + name + " " + surname + '</b>,<br/><br/>jūs buvote pridėtas prie sistemos norint prisijungti pirmiausiai reikės prisiregistruoti.<br/>Registruotis galite <a href="https://linas-semiotas.github.io/react-project/">čia</a>, jūsų indentifikacinis kodas yra:<br/><br/><b>' + username + "</b>"
          })
          alert("Vartotojas sėkmingai pridėtas");
        }
      })
    );
  }
  else{
    alert("Yra tuščių laukelių");
  }
}